.playerBlock{
    color:#fff;
}
.greatRating,.normalRating{
    position: absolute;
    top: -20px;
    right: -20px;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 28px;
    font-weight: bold;

    color: #e9cc74;
    border-radius: 50%;
    filter: drop-shadow(1px 1px 12px #e9cc74);
}
.greatRating{
    background-color: #466b4b;
}
.normalRating{
    background-color: #ce3636;
}